.quote-text {
  font-weight: 400;
}

.quote-footer {
  margin: 10px 0 10px 0;
  font-size: 20px;
}

.icon {
  margin-top: 10px;
  float: left;
}

.fa-quote-left {
  position: relative;
  top: 34px;
}

.fa-quote-right {
  position: relative;
  bottom: 23px;
  float: right;
}

.quote-next {
  color: #fff;
  width: 70px;
  font-size: 24px;
  float: right;
  margin-top: 16px;
  padding: 5px 0;
  background-color: transparent;
  border: 1px solid;
}

.quote-next:focus {
  outline: none;
}

.bottom {
  position: fixed;
  top: 70%;
  left: 50%;
  /* bring your own prefixes */
  transform: translate(-50%, -50%);
  text-align: center;
  color: #aaded0;
}

.example-twitter {
  padding: 15px;
  margin: 100px 0 0.5em;
  color: #333;
  background: #eee;
  opacity: 0.75;
  /* css3 */
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  border-radius: 10px;
}

/* creates the triangle */
.example-twitter:after {
  content: "";
  position: absolute;
  top: -30px;
  left: 50px;
  border: 15px solid transparent;
  border-bottom-color: #eee;
  /* reduce the damage in FF3.0 */
  display: block;
  width: 0;
}
