.form {
  position: fixed;
  top: 50%;
  left: 50%;
  height: 50%;
  width: 50%;
  margin: -15% 0 0 -25%;
}

.form-group {
  text-align: left;

}

.form-group label {
  font-weight: bold;
}

.cardStyle {
  height: 100;
  width: 100;
  display: center;
}

.profile {
  border: 5px;
  border-color: black;
}

.parent {
  display: flex;
}
.narrow {
  width: 200px;
  margin: 5px;
}
.wide {
  flex: 1;
}

.home {
  position: fixed;
  top: 50%;
  left: 50%;
  /* bring your own prefixes */
  transform: translate(-50%, -50%);
}

.white {
  color: white;
}

.about-title {
  position: fixed;
  top: 15%;
  left: 50%;
  /* bring your own prefixes */
  transform: translate(-50%, -50%);
}

.img {
  border-radius: 50%;
  height: 500px;
  width: 500px;
}

.centered {
  position: fixed;
  top: 50%;
  left: 50%;
  width: 500px;
  /* bring your own prefixes */
  transform: translate(-50%, -50%);
}

.centered-text {
  position: fixed;
  top: 50%;
  left: 50%;
  width: 400px;
  /* bring your own prefixes */
  transform: translate(-50%, -50%);
}

.centered-left {
  position: fixed;
  top: 50%;
  left: 65%;
  width: 500px;
  /* bring your own prefixes */
  transform: translate(-50%, -50%);
  border: 10px solid #ddd; /* Gray border */
}

.centered-right {
  position: fixed;
  top: 50%;
  left: 35%;
  width: 500px;
  /* bring your own prefixes */
  transform: translate(-50%, -50%);
  border: 10px solid #ddd; /* Gray border */

}