.App {
  text-align: center;
}

.socials {
  padding: 1px;
  margin: 3px;
}

.form {
  position: fixed;
  top: 50%;
  left: 50%;
  height: 50%;
  width: 50%;
  margin: -15% 0 0 -25%;
}

.form-group {
  text-align: left;

}

.form-group label {
  font-weight: bold;
}

.cardStyle {
  height: 100;
  width: 100;
  display: center;
}

.profile {
  border: 5px;
  border-color: black;
}

.parent {
  display: flex;
}
.narrow {
  width: 200px;
  margin: 5px;
}
.wide {
  flex: 1 1;
}

.home {
  position: fixed;
  top: 50%;
  left: 50%;
  /* bring your own prefixes */
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.white {
  color: white;
}

.about-title {
  position: fixed;
  top: 15%;
  left: 50%;
  /* bring your own prefixes */
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.img {
  border-radius: 50%;
  height: 500px;
  width: 500px;
}

.centered {
  position: fixed;
  top: 50%;
  left: 50%;
  width: 500px;
  /* bring your own prefixes */
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.centered-text {
  position: fixed;
  top: 50%;
  left: 50%;
  width: 400px;
  /* bring your own prefixes */
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.centered-left {
  position: fixed;
  top: 50%;
  left: 65%;
  width: 500px;
  /* bring your own prefixes */
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  border: 10px solid #ddd; /* Gray border */
}

.centered-right {
  position: fixed;
  top: 50%;
  left: 35%;
  width: 500px;
  /* bring your own prefixes */
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  border: 10px solid #ddd; /* Gray border */

}
.quote-text {
  font-weight: 400;
}

.quote-footer {
  margin: 10px 0 10px 0;
  font-size: 20px;
}

.icon {
  margin-top: 10px;
  float: left;
}

.fa-quote-left {
  position: relative;
  top: 34px;
}

.fa-quote-right {
  position: relative;
  bottom: 23px;
  float: right;
}

.quote-next {
  color: #fff;
  width: 70px;
  font-size: 24px;
  float: right;
  margin-top: 16px;
  padding: 5px 0;
  background-color: transparent;
  border: 1px solid;
}

.quote-next:focus {
  outline: none;
}

.bottom {
  position: fixed;
  top: 70%;
  left: 50%;
  /* bring your own prefixes */
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  text-align: center;
  color: #aaded0;
}

.example-twitter {
  padding: 15px;
  margin: 100px 0 0.5em;
  color: #333;
  background: #eee;
  opacity: 0.75;
  /* css3 */
  border-radius: 10px;
}

/* creates the triangle */
.example-twitter:after {
  content: "";
  position: absolute;
  top: -30px;
  left: 50px;
  border: 15px solid transparent;
  border-bottom-color: #eee;
  /* reduce the damage in FF3.0 */
  display: block;
  width: 0;
}

